import Create from "./pages/Create";
import Layout from "./components/Layout";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom"; //imported for using router in browser
import { ThemeProvider } from "@emotion/react";
import { createTheme } from "@mui/material";
import Inbox from "./pages/Inbox";
import Dashboard from "./pages/Dashboard";

const theme = createTheme({
  palette: {
    primary: {
      main: "#fefefe",
    },
  },
  typography: {
    fontFamily: "Quicksand",
    fontWeightLight: 300,
    fontWeightMedium: 400,
    fontWeightRegular: 500,
    fontWeightBold: 600,
  },
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <Router>
        <Layout>
          {/* encapsulates all the components inside to apply routing to them */}

          <Routes>
            {" "}
            {/* all the componets inside will be rendered according to the routes given to them */}
            <Route path="/create" element={<Create />} />
            <Route path="/" element={<Inbox />} />
            <Route path="/dashboard" element={<Dashboard />} />
            {/* //render the element prop when the address matches the path */}
            {/* <Route path="*" element={<HelloWorld />} /> */}
          </Routes>
        </Layout>
      </Router>
    </ThemeProvider>
  );
}

export default App;
