import React, { useState } from "react";
import { Typography, Button, Container, TextField } from "@mui/material";
import EmailOutlinedIcon from "@mui/icons-material/EmailOutlined";
import axios from "axios";

const styledTextField = {
  marginTop: 20,
  marginBottom: 20,
  display: "block",
};

//created a function and exporting it in the last line
function Create() {
  //creating states for form
  const [email_address, set_email_address] = useState(""); //initialize email address as an empty string
  const [content, set_content] = useState("");
  const [subject, set_subject] = useState("");
  const [email_address_error, set_email_address_error] = useState(false);
  const [content_error, set_content_error] = useState(false);
  const [email_sent, set_email_sent] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault(); //to prevent default

    set_content_error(false);
    set_email_address_error(false); //this sets the state to false if it was true before and checks again in the next line for error

    if (email_address_error === "") {
      set_email_address_error(true); //set the boolean true if there is an empty string
    }

    if (content_error === "") {
      set_content_error(true);
    }

    if (email_address && content) {
      axios
        .post("http://localhost:8000/emails", {
          email_address: email_address,
          subject: subject,
          content: content,
        })
        .then(() => {
          set_email_sent(true);
        });
    }
    if (email_sent) {
      set_content("");
      set_email_address("");
      set_subject("");
    }
  };

  return (
    // created a container to hold all the components
    <Container>
      {/* Typography to change all the fonts in the page */}
      <Typography
        variant="h6" //variant to select which font size
        color="textSecondary"
        component="h2" //render the h6 variant as a h2 components
        gutterBottom //creates a false bottom
      >
        Write an Email
      </Typography>
      <form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <TextField
          style={styledTextField} //can only pass js objects
          onChange={(e) => {
            set_email_address(e.target.value);
          }}
          required
          fullWidth
          label="Email Address"
          variant="outlined"
          color="secondary"
          error={email_address_error} //passing the object to be displayed as error
        />
        <TextField
          style={styledTextField} //can only pass js objects
          onChange={(e) => {
            set_subject(e.target.value);
          }}
          fullWidth
          label="Subject"
          variant="outlined"
          color="secondary"
        />
        <TextField
          style={styledTextField}
          onChange={(e) => {
            set_content(e.target.value);
          }}
          required
          fullWidth
          label="Content"
          variant="outlined"
          color="secondary"
          multiline
          rows={4}
          error={content_error}
        />
        <Button
          type="submit"
          variant="contained"
          color="secondary"
          disableElevation //removes the shadow
          startIcon={<EmailOutlinedIcon />} //adds an icon at the start of the button text also has a prop to add an endIcon
        >
          Send
        </Button>
        {email_sent && <p color="error">Your email is sent</p>}
        {/* gives an text after the email is sent */}
      </form>
    </Container>
  );
}

export default Create;
