import React, { useEffect, useState } from "react";
import axios from "axios";
import { Container } from "@mui/material";
import Cards from "../components/Cards";
import { Masonry } from "@mui/lab";
function Inbox() {
  //created a state to enter the json data after fetching initiated with an empty string
  const [emails, set_emails] = useState([]);

  //useEffect hook to fetch some data with the help of axios
  useEffect(() => {
    axios
      .get("http://localhost:8000/inbox/")
      .then((response) => {
        set_emails(response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  const handleDelete = async (id) => {
    console.log(id);
    await axios.delete("http://localhost:8000/inbox/delete/" + id); //axios puts a delete request on the server with id as an argument in url

    const newMails = emails.filter((email) => email.id !== id); //filters out the id that was deleted
    set_emails(newMails); //updates the state with the filtered out emails
  };

  return (
    <div>
      <Container
        style={{ maxWidth: "100%", marginTop: 12 }}
        //adds a margin to the content inside it
      >
        <Masonry columns={{ xs: 1, lg: 4, md: 3 }} spacing={3}>
          {emails.map((element) => (
            <div key={element.id}>
              <Cards emails={element} handleDelete={handleDelete} />
            </div>
          ))}
        </Masonry>
      </Container>
    </div>
  );
}

export default Inbox;
