import React from "react";
import {
  Card,
  CardContent,
  CardHeader,
  CardActions,
  IconButton,
  Typography,
  Button,
} from "@mui/material";
import { DeleteOutlined } from "@mui/icons-material";
import Modal from "./Modal";

//passed emails and handleDelte funtion as props in the cards components

function Cards({ emails, handleDelete }) {
  //creating states for opening modal
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  let Tag;
  if (emails.tag === "Personal") {
    Tag = (
      <div
        style={{
          backgroundColor: "aqua",
          borderRadius: "15px",
          padding: "5px",
          width: "fit-content",
          fontSize: "10px",
          color: "grey",
        }}
      >
        {emails.tag}
      </div>
    );
  } else {
    Tag = (
      <div
        style={{
          backgroundColor: "red",
          borderRadius: "15px",
          padding: "5px",
          width: "fit-content",
          fontSize: "10px",
          color: "grey",
        }}
      >
        {emails.tag}
      </div>
    );
  }

  return (
    <div>
      <Card>
        <CardHeader
          titleTypographyProps={{ variant: "h6" }}
          title={emails.subject}
          subheader={emails.recipients}
          action={
            <IconButton
              onClick={() => {
                handleDelete(emails.id); //function to delete emails with argument of id sends it to the handle delete in inbox.js
              }}
              style={{ right: "0" }}
            >
              <DeleteOutlined />
            </IconButton>
          }
        />
        <CardContent>
          <div>{Tag}</div>
          <Typography color="textSecondary" variant="body2">
            <div
              dangerouslySetInnerHTML={{
                __html:
                  emails.body.slice(0, Math.ceil(emails.body.length / 2)) +
                  "...",
              }}
            />
          </Typography>
        </CardContent>
        <CardActions>
          <Button
            onClick={handleOpen}
            style={{
              borderRadius: "6px",
              color: "turquoise",
            }}
          >
            Show more
          </Button>
          <Modal open={open} handleClose={handleClose} emails={emails} />
        </CardActions>
      </Card>
    </div>
  );
}

export default Cards;
